import React from 'react'
import { StyledProduct } from '../styles/Style-Components'

export class Product extends React.Component {
  render() {
    const { photo, brand, description, price } = this.props
    return (
      <StyledProduct className='col-4 col-m-4'>
        <img className='productPhoto'
          src={require( `../assets/images/${photo}`)}
          alt={description}/>
        <h4>
          <span>{brand}</span>
          <span>{price}</span>
        </h4>
        <p className='product'>{description}</p>
      </StyledProduct>
    )
  }
}

export default Product



