import React from 'react'
import Title from '../components/Title'
import Background from '../components/Background'
import ProductsWrapper from '../containers/ProductsWrapper'
import { StyledSection } from '../styles/Style-Containers';
import '../styles/App.scss'
import { products } from '../ProductsData';

const lingerie = [
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-1.png'},
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-1.png'},
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-1.png'},
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-2.png'},
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-2.png'},
  { brand: 'Triumph', description: 'Lorem ipsum se dolor sit', price: '49,00', photo: 'chantelle-2.png'},
]

class Section3 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showProducts: false,
    }
    this.toggleState = this.toggleState.bind(this);
  }
  toggleState() {
    this.setState({
      showProducts: true,
    })
  }
  render() {
    return (
      <StyledSection className='section3'>
        {this.state.showProducts ?
          <ProductsWrapper
            coverPhoto='1-lingerie.png'
            theme='Verlijdelijke lingerie'
            description='Lorem ipsum se dolor sit amet condecteur sit amet dolorose'
            products={ lingerie }
          /> : <div>
          <div className="contentWrapper">
            <Title
              action={this.toggleState}
              sectionTitle='Trend 1'
              theme='Verlijdelijke lingerie'
              class='section3'
            />
          </div>
          <Background
            photo1src='1-lingerie.png'
            photo1desc='lingerie cover style'
            photo2src='2-lingerie.png'
            photo2desc='lingerie cover style'
            class='section3'
          />
        </div> }
      </StyledSection>
    )
  }
}

export default Section3



