import React from 'react'
import CarouselItem from '../components/CarouselItem'
import { StyledCarousel } from '../styles/Style-Containers';
import '../styles/App.scss'

class Carousel extends React.Component {


  renderSlide(slide, index) {
    var index=1
    return <CarouselItem key={ index } { ...slide }/>
    }

  render() {
    return (
      <StyledCarousel index={this.props.index} swipeable autoScroll overscrollable>
        {this.props.slides.map(this.renderSlide.bind(this))}
      </StyledCarousel>
    )
  }
}

export default Carousel



