import React from 'react'
import Title from '../components/Title'
import Background from '../components/Background'
import Navigation from '../components/Navigation'
import { StyledSection } from '../styles/Style-Containers';
import '../styles/App.scss'

class HeadSection extends React.Component {
  render() {
    return (
      <StyledSection className='section1'>
        <div className='contentWrapper'>
          <div className='logo'>
            <img src={require('../assets/images/logo-bijenkorf.svg')} alt='de Bijenkorf logo'/>
          </div>
          <Navigation />
          <Title
            sectionTitle='Make it'
            theme='You'
            class='headSection'
          />
        </div>
        <Background
          photo1src='woman.png'
          photo1desc='make it you cover style woman'
          photo2src='man.png'
          photo2desc='make it you cover style man'
          class='headSection'
        />
      </StyledSection>
    )
  }
}

export default HeadSection



