import styled from 'styled-components'
import '../styles/App.scss'

export const StyledNavigation = styled.div `
  z-index: 3;
  display: block;
  text-align: center;
  a {
    text-transform: uppercase;
    font-family: Proxima Nova;
  }
`
export const StyledButton = styled.div `
  flex-wrap: nowrap;
  margin-right: auto;
  border-style: solid;
  border-width: thin;
  max-width: 500px;
  padding: 15px 40px;
  text-align: center;
  font-family: ProximaNova,Arial,sans-serif;
  font-weight: bold;
  font-size: 0.8em;
  opacity: 0.8;
  text-decoration: none;
  text-transform: lowercase;
  letter-spacing: .1em;
`

export const StyledTitle = styled.div `
  text-align: center;
  z-index: 50;
  margin: auto;
  &.section3 h1:hover {
    background-color: #F9F9F9;
  }
  h3 {
    font-family: Proxima Nova,Arial,sans-serif;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: .38em;
    line-height: 1.3;
    margin-top: 0;
    margin-bottom: 5px;
    &.headSection {
      font-size: 5vh;
      opacity: 0.7;
    }
    &.section3 {
      font-size: 2vh;
    }
  }
  h1 {
    font-family: Velino Display,Georgia,serif;
    font-weight: normal;
    text-transform: uppercase;
    margin: auto;
    font-size: 5vh;
    letter-spacing: .1em;
    &.headSection {
      font-size: 25vh;
      font-weight: bold;
      font-weight: lighter;
      opacity: 0.6;
      padding-bottom: 5vh;
      letter-spacing: -.05em;
    }
    &.section3 {
      word-spacing: 80vw;
      line-height: 1.3;
      text-align: center;
    }
  }
  &.section5 {
    margin: 10px 0;
  }
`
export const StyledBackground = styled.div `
  max-height: 100vh;
  margin: auto;
  overflow: hidden;
  img {
    &#photo1 {
      position: absolute;
      z-index: -22;
      width: auto;
      overflow: hidden;
      &.headSection {
        height: 127%;
        left: 0%;
        bottom: 0%;
      }
      &.section3 {
        height: 100%;
        left: 5%;
        bottom: 0%;
      }
    }
    &#photo2 {
      margin: auto;
      position: absolute;
      z-index: -21;
      width: auto;
      &.headSection {
        height: 150%;
        right: -10%;
        bottom: -9%;
        top: 0;
      }
      &.section3 {
        height: 80%
        left: 40%;
        bottom: 0%;
      }
    }
  @media screen and (orientation:portrait) {
      &#photo1 {
        left: 0%;
        background-color: #F9F9F9;
        opacity: 1;
        animation: fadeinout 1s ease-in-out infinite 8s alternate;
        -moz-animation: fadeinout 1s ease-in-out infinite 8s alternate;
        -webkit-animation: fadeinout 1s ease-in-out infinite 8s alternate;
      }
      &#photo2 {
        left: -85%;
        opacity: 0;
      }
    }
  }
  @-webkit-keyframes fadeinout {
    0% { opacity: 1; }
    45% {opacity: 1; }
    55% {opacity: 0; }
    100% {opacity: 0; }
  }
  @keyframes fadeinout {
    0% { opacity: 1; }
    45% {opacity: 1; }
    55% {opacity: 0; }
    100% {opacity: 0; }
  }
`;

export const StyledProduct = styled.div `
  display: flex;
  flex-direction: column;
  padding-left: 5%;
  padding-right: 5%;
  img {
    width: 100%;
    margin: auto;
  }
  h4 {
    font-family: Velino Display,Georgia,serif;
    font-style: italic;
    font-size: 18px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0;
  }
  p.product {
    font-size: 12px;
    opacity: 0.6;
  }
`



