import React from 'react'
import Title from '../components/Title'
import Carousel from '../containers/Carousel'
import { StyledSection } from '../styles/Style-Containers';
import '../styles/App.scss'

const essentials = [
  { slideNumber: '1', brand: 'See by Chloé', description: 'De retro Lizzie handtas van See By Chloé is duidelijk geinspired op de jaren zeventig. Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.', productPhoto: 'see-by-chloe-1.png', stylePhoto:'see-by-chloe-2.jpg', tag:'De retro Lizzie handtas van See By Chloé', color:'#EECCC0'},
  { slideNumber: '2', brand: 'Sandro', description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.', productPhoto: 'sandro.png', stylePhoto:'sandro-2.jpg', tag:'Lorem ipsum dolor sit amet, consectetur adipiscing elit', color:'#393939'},
  { slideNumber: '3', brand: 'Daniel Wellington', description: 'De Daniel Wellington Classic Glasgow is een prachtig vormgegeven horloge met een mooie Nylon NATO band. De horloges zijn steeds beschikbaar in een rose of zilverkleurige metaal afwerking', productPhoto: 'daniel-wellington-1.png', stylePhoto:'daniel-wellington-2.jpg', tag:'De Daniel Wellington Classic Glasgow', color:'	#30325B'},
]

class Section5 extends React.Component {
  render() {

    return (
      <StyledSection className='section5'>
        <div className='contentWrapper'>
          <Title
            sectionTitle='Our top 10'
            theme='Essentials'
            class='section5'
          />
          <Carousel
            slides = { essentials }
            index = '1'
          />
        </div>
      </StyledSection>
    )
  }
}

export default Section5



