import React, { PureComponent } from 'react'
// import DeviceOrientation,{ Orientation } from 'react-screen-orientation'
import style from '../styles/App.scss'
import { StyledBackground } from '../styles/Style-Components'

class Background extends PureComponent {
  render() {
    return (
      <StyledBackground>
            <img
              id="photo1"
              src={require( `../assets/images/${this.props.photo1src}`)}
              alt={this.props.photo1desc}
              className={this.props.class}
            />
            <img
              id="photo2"
              src={require( `../assets/images/${this.props.photo2src}`)}
              alt={this.props.photo2desc} className={this.props.class}/>
      </StyledBackground>
    )
  }
}

export default Background



