import React from 'react'
import Button from './Button'
import { StyledCarouselItem } from '../styles/Style-Containers'
import '../styles/App.scss'

// const colorStyle = {
//   backgroundColor: {color},
// };

export class CarouselItem extends React.Component {
  render() {
    const { slideNumber, brand, description, productPhoto, stylePhoto, tag, color } = this.props
    return (
      <StyledCarouselItem className='col-12'>

        <div className='bottomLayer' style={{
          background: `linear-gradient(to right, ${color} 60%, #FFFFFF 40%)`
        }}>
          <h1 className='slideNumber'>{slideNumber}</h1>
          <img className='stylePhoto' src={require( `../assets/images/${stylePhoto}`)}
          alt={tag}/>
        </div>

        <div className='topLayer'>
          <img className='arrow previous' src={require('../assets/images/arrow-previous.svg')}/>
          <div className='left-text col-m-3 col-4'>
            <h1>{brand}</h1>
            <p>{description}</p>
            <Button className='white' label='bekijk product'/>
          </div>
          <div className='right-photo col-m-5 col-4'>
            <img className='productPhoto'
              src={require( `../assets/images/${productPhoto}`)}
              alt={tag}/>
          </div>
          <div className='productTag'><h6>{tag}</h6></div>
          <img className='arrow next' src={require('../assets/images/arrow-next.svg')}/>
        </div>

      </StyledCarouselItem>
    )
  }
}

export default CarouselItem



