import React from 'react'
import Title from '../components/Title'
import { StyledSection } from '../styles/Style-Containers';
import '../styles/App.scss'

class Section2 extends React.Component {
  render() {
    return (
      <StyledSection className='section2'>
        <div className='contentWrapper'>
          <Title
            sectionTitle='Kies je stijl'
            theme='Make it you'
            class='section2'
          />
        </div>
      </StyledSection>
    )
  }
}

export default Section2



