import React from 'react'
import { Link } from 'react-router'
import Product from '../components/Product'
import Button from '../components/Button'
import { StyledProductsWrapper } from '../styles/Style-Containers';
import '../styles/App.scss'

class ProductsWrapper extends React.Component {
  renderProduct(product, index) {
      return <Product key={ index } { ...product }/>
    }
  render() {
    const { coverPhoto, theme, products, description, label, url } = this.props
    return (
      <StyledProductsWrapper>
        <img
          className='coverPhoto'
          src={require( `../assets/images/${coverPhoto}`)}
          alt={`${theme} cover photo`}/>
        <div className='col-8 col-m-12 contentContainer'>
          <div className='productsContainer'>
            {products.map(this.renderProduct.bind(this))}
          </div>
          <h1>{theme}</h1>
          <p className='subtitle'>{description}</p>
          <Button label={'bekijk hele collectie'}/>
        </div>
      </StyledProductsWrapper>
    )
  }
}

export default ProductsWrapper



