import React, { PureComponent } from 'react'
// import '../styles/App.scss'
import { StyledTitle } from '../styles/Style-Components'

class Title extends PureComponent {
  render() {
    return (
      <StyledTitle className={ this.props.class } onClick={ this.props.action }>
        <h3 className={ this.props.class }>{ this.props.sectionTitle }</h3>
        <h1 className={ this.props.class }>{ this.props.theme }</h1>
      </StyledTitle>
    )
  }
}

export default Title



