import styled from 'styled-components'
import '../styles/App.scss'

export const StyledSection = styled.div `
  height: 100vh;
  position: relative;
  overflow: hidden;
  div.contentWrapper {
      display: flex;
      flex-direction: column;
      height: 100vh;
      overflow: hidden;
      position: relative;
  }
`
export const StyledProductsWrapper = styled.div `
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  img.coverPhoto {
    position: absolute;
    right: 60%;
    bottom: 0%;
    z-index: -5;
    height: 100%;
    width: auto;
    @media only screen and (max-width: 768px) {
      display: none;
    }
  }
  div.contentContainer {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    padding-top: 40px;
    padding-bottom: 20px;
    overflow: hidden;
    max-height: 100vh;
  }
  div.productsContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding-bottom: 20px;
    justify-content: space-between;
    overflow: scroll;
  }
  h1 {
    font-family: Velino Display, Georgia,serif;
    font-style: italic;
    opacity: 0.3;
    font-size: 2.8em;
    margin-bottom: 0;
    padding-left: 5%;
  }
  p.subtitle {
    font-size: 18px;
    opacity: 0.6;
    padding-left: 5%;
  }
`
export const StyledCarousel = styled.div `
  position: relative;
  margin-bottom: 5%;
  display: flex;
  ${''/* height: 100%; */}
`
export const StyledCarouselItem = styled.div `
display: flex;
flex-direction: column;
margin-top: 20px;
margin-bottom: 20px;
position: relative;
div.bottomLayer {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: flex-start;
  padding-right: 5%;
  z-index: -10;
  h1.slideNumber {
    margin-top: 5%;
    font-family: Velino Display, serif;
    font-style: italic;
    font-size: 450px;
    color: #FFF;
    opacity: 0.2;
  }
  img.stylePhoto {
    margin-top: 5%;
    width: auto;
    height: 150px;
  }
}
div.topLayer {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  flex-wrap: nowrap;
  div.left-text {
    color: white;
    text-align: left;
    padding-right: 5%;
    h1 {
      font-family: Velino Display,Georgia,serif;
      font-weight: normal;
      font-size: 50px;
      text-transform: capitalize;
      letter-spacing: .1em;
    }
    p {
      font-size: 14px;
    }
  }
  h6 {
    font-family: Velino Display,Georgia,serif;
    font-style: italic;
    font-size: 10px;
  }
  img.productPhoto {
    height: 50vh;
  }
  img.previous {
    transform: rotate(180deg);
    }
  }
}
`



