import React from 'react'
import Title from '../components/Title'
import { StyledSection } from '../styles/Style-Containers';
import '../styles/App.scss'

class Section4 extends React.Component {
  render() {
    return (
      <StyledSection className='section4'>
        <div className='contentWrapper'>
          <Title
            sectionTitle='Trend 2'
            theme='Veelzijdig kant'
            class='section4'
          />
        </div>
      </StyledSection>
    )
  }
}

export default Section4



